import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { SharedService } from './shared.service';

@Injectable({
  providedIn: 'root',
})
export class GeneralService {
  apiUrl = this.sharedService.getUrl;
  apiUrl2 = this.sharedService.getUrl2;//admin
  constructor(private http: HttpClient, public sharedService: SharedService) { }
  GetUserMenu(body: any) {
    return this.http.get(
      this.apiUrl + `Cashier/GetUserMenu`,
      this.sharedService.getHeaders()
    );
  }
  getLookupsById(id: any) {
    return this.http.get(
      this.apiUrl + 'Lookups/GetLookups?lookupTypeId=' + id + '&pageSize=1000',
      this.sharedService.getHeaders()
    );
  }
  getLookupsWithParams(params) {
    return this.http.get(
      this.apiUrl + `Lookups/GetLookups${params}`,
      this.sharedService.getHeaders()
    );
  }
  getLookupsStatus() {
    return this.http.get(
      this.apiUrl + 'Lookups/GetLookups?lookupTypeId=2&parentId=1',
      this.sharedService.getHeaders()
    );
  }


  GetUserDetails(id: number) {
    return this.http.get(
      this.apiUrl + `User/GetUSERDetails?userId=${id}`,
      this.sharedService.getHeaders()
    );
  }
  getUserPermissions(userId, portalId = 17001) {
    return this.http.get(
      this.apiUrl + `User/GetUserPermissions?userId=${userId}&portalId=${portalId}`,
      this.sharedService.getHeaders()
    );
  }
  getUserPortals() {
    return this.http.get(
      this.apiUrl + `User/GetUserPortals`,
      this.sharedService.getHeaders()
    );
  }
  GetUserMenuNew(portalId) {
    return this.http.get(
      this.apiUrl + `User/GetUserMenuNew?portalId=${portalId}`,
      this.sharedService.getHeaders()
    );
  }
  EditUser(body: any) {
    return this.http.post(
      this.apiUrl + `User/EditUser`,
      body,
      this.sharedService.getHeaders()
    );
  }
  editUser(body: any) {
    return this.http.post(
      this.apiUrl2 + `/api/User/EditUser`,
      body,
      this.sharedService.getHeaders()
    );
  }

}
